* {
  box-sizing: border-box;
}

* .page {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
}

.catalog {
  grid-template-rows: 1fr 1fr;
  grid-template-columns: repeat(4, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width <= 487px) {
  .catalog {
    grid-template-columns: repeat(1, 200px);
  }
}

@media (width >= 488px) {
  .catalog {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .catalog {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .catalog {
    grid-template-columns: repeat(4, 200px);
  }
}

.header {
  color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  font-weight: 500;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo {
  width: 40px;
  height: 40px;
  display: block;
}

.header__logo-image {
  width: 100%;
  height: 100%;
}

.nav {
  display: block;
}

.nav__list {
  text-transform: uppercase;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item:not(:first-child) {
  margin-left: 20px;
}

.nav__link {
  color: #060b35;
  white-space: nowrap;
  align-items: center;
  height: 60px;
  line-height: 60px;
  text-decoration: none;
  transition: color .3s;
  display: flex;
  position: relative;
}

.nav__link:hover {
  color: #00acdc;
}

.nav__link:active {
  color: #585858;
}

.is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
  transition: all .3s;
}

.card__title {
  color: #060b35;
  margin-top: 0;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  transition: all .3s;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__title {
  color: #34568b;
}

.card__picture {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__code {
  color: #616070;
  margin-top: 0;
  margin-bottom: 16px;
  font-size: 10px;
  line-height: 14px;
}

.card__review-box {
  justify-content: space-between;
  align-items: center;
  height: 16px;
  margin-top: 0;
  margin-bottom: 24px;
  display: flex;
}

.card__review-box-content {
  font-size: 10px;
  line-height: 14px;
}

.card__price-box {
  justify-content: space-between;
  align-items: center;
  height: 18px;
  margin-top: 0;
  margin-bottom: 16px;
  display: flex;
}

.card__price-box-text {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.card__price-box-content {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__purchase-button {
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border-color: #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  margin-top: 0;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition-duration: .3s;
  display: flex;
}

.card__purchase-button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star-active.edda4c43.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:last-child {
  background-image: url("star.822d561e.svg");
  margin-right: 0;
}
